<!-- eslint-disable vue/no-v-html -->
<template>
  <div>
    <el-dialog
      :title="$t('ann.detail')"
      :visible.sync="v"
      :close-on-click-modal="false"
      width="840px"
      top="6%"
      append-to-body
    >
      <div class="ann-title">{{ detail.bulletinTitle }}</div>
      <div class="ann-time">
        <span>{{ $t('ann.createDate') + detail.publishDate }}</span>
        <span>{{ $t('ann.updateDate') + detail.updateTime }}</span>
      </div>
      <div class="ann-content" v-html="detail.bulletinContent"></div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" size="small" @click="clickOk">{{
          $t('ann.back')
        }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import fetch from '@/request/fetch'
export default {
  components: {},
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    bulletinId: {
      type: [Number, String],
      default: '',
    },
    readIt: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      detail: {},
    }
  },
  computed: {
    v: {
      get: function () {
        return this.visible
      },
      set: function (value) {
        this.$emit('update:visible', value)
      },
    },
  },
  watch: {
    async visible(val) {
      if (!val) {
        this.detail = {}
        if (this.readIt) {
          this.read()
        }
      }
      this.detail = await fetch(
        '/bulletin/queryBulletinInfo',
        { bulletinId: this.bulletinId },
        {
          method: 'get',
        }
      )
    },
  },
  methods: {
    clickOk() {
      this.v = false
    },
    async read() {
      await fetch('/bulletinRcvObjRel/updateBulletinRcvObjStatus', [this.bulletinId], {
        method: 'post',
      })
    },
  },
}
</script>
