<template>
  <el-row class="summary">
    <el-col
      v-for="(item, index) in info"
      :key="item.type"
      class="summary-container"
      :span="5"
    >
      <div class="summary-item">
        <div class="summary-item-type">
          {{ item.type }}
          <svg-icon :icon="item.icon" width="14" height="14"></svg-icon>
        </div>
        <div class="summary-item-num">{{ item.num }}</div>
        <!-- <div class="summary-item-right">
          <svg-icon class="summary-item-right-icon" icon="sim"></svg-icon>
        </div> -->
      </div>
      <div class="dashed-line" :class="{ lastLine: index === info.length - 1 }"></div>
    </el-col>
  </el-row>
</template>

<script>
import fetch from '@/request/fetch'
export default {
  props: {},
  data() {
    return {
      lifeRes: [],
    }
  },
  computed: {
    info() {
      let num1 = ''
      let num2 = ''
      let num4 = ''
      let num5 = null
      this.lifeRes.forEach((item) => {
        if (item.statusCd === '100000') {
          // 在用
          num1 = item.statusCdCount
        }
        if (item.statusCd === '120000') {
          // 停机
          num2 = item.statusCdCount
        }
        if (item.statusCd === '140002') {
          // 未激活
          num4 = item.statusCdCount
        }
        if (item.statusCd === '300001') {
          // 已实名
          num5 = item.statusCdCount
        }
      })
      const list = [
        {
          type: this.$t('simMag.use'),
          icon: 'sim_using',
          num: num1,
        },
        {
          type: this.$t('simMag.stop'),
          icon: 'sim_stop',
          num: num2,
        },
        {
          type: this.$t('simMag.unactivated'),
          icon: 'sim_unactivated',
          num: num4,
        },
      ]
      if (num5 != null) {
        list.push({
          type: this.$t('simMag.identityStatus1'),
          icon: 'sim_realnamed',
          num: num5,
        })
      }
      return list
    },
  },
  mounted() {
    this.getSimCardLifeCycle()
  },
  methods: {
    async getSimCardLifeCycle() {
      const res = await fetch(
        '/prodInst/getSimCardLifeCycle',
        {},
        {
          method: 'get',
        }
      )
      this.lifeRes = res
    },
  },
}
</script>

