<template>
  <div class="container">
    <div class="container-form">
      <ct-section-title :title="$t('simMag.simStatus')" />
      <SimStatus></SimStatus>
      <el-form ref="qryParamRef" :model="qryParam" size="small">
        <div class="row-container">
          <div class="left-row">
            <el-row :gutter="20">
              <el-col :span="6">
                <el-form-item label="ICCID" prop="iccid">
                  <el-input
                    v-model="qryParam.iccid"
                    :placeholder="inputPh('ICCID')"
                    maxlength="20"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="MSISDN" prop="msisdn">
                  <el-input
                    v-model="qryParam.msisdn"
                    :placeholder="inputPh('MSISDN')"
                    maxlength="20"
                  >
                    <ct-async-select
                      slot="prepend"
                      v-model="qryParam.areaCode"
                      url="/user/areaCodeList"
                      :placeholder="$t('accountMag.areaCode')"
                      method="get"
                      strkv
                      :style="areaCodeStyle"
                    ></ct-async-select>
                  </el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="IMSI" prop="imsi">
                  <el-input
                    v-model="qryParam.imsi"
                    :placeholder="inputPh('IMSI')"
                    maxlength="20"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item :label="$t('simMag.simStatus')" prop="statusCd">
                  <ct-select
                    v-model="qryParam.statusCd"
                    :placeholder="selectPh('simMag.simStatus')"
                    :options="statusOptions"
                  >
                  </ct-select>
                </el-form-item>
              </el-col>
              <div v-show="powerSearch" class="more-query-group">
                <el-col :span="6">
                  <el-form-item :label="$t('simMag.addressId')" prop="addressId">
                    <ct-async-select
                      v-model="qryParam.addressId"
                      :placeholder="selectPh('simMag.addressId')"
                      url="/prodInst/getSupplierListForQuery"
                      method="get"
                      label-key="attrValueName"
                      value-key="attrValue"
                    ></ct-async-select>
                  </el-form-item>
                </el-col>
                <el-col style="width: 350px">
                  <el-form-item :label="$t('simMag.actDate')" prop="actBeforeDate">
                    <ct-date-picker
                      :begin.sync="qryParam.actBeforeDate"
                      :end.sync="qryParam.actAfterDate"
                    >
                    </ct-date-picker>
                  </el-form-item>
                </el-col>
              </div>
            </el-row>
          </div>
          <div class="right-row">
            <el-form-item>
              <div class="search-button-group">
                <el-dropdown
                  split-button
                  type="primary"
                  @click="querySimCardListForPage(false)"
                  ><i class="el-icon-search" style="margin-right: 5px" />{{
                    $t('common.query')
                  }}
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item @click.native="showUpload = true">{{
                      $t('common.batchQuery')
                    }}</el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
                <el-button icon="el-icon-refresh" @click="reset">{{
                  $t('common.reset')
                }}</el-button>
                <show-more-button v-model="powerSearch" />
              </div>
            </el-form-item>
          </div>
        </div>
        <div class="tool-button-group">
          <BatchExport
            v-compPriv="3"
            :query-id="2040"
            :params="simListExportParam"
            :disabled="tableData.length === 0"
          ></BatchExport>
        </div>
      </el-form>
      <el-table
        ref="table"
        v-loading="loading"
        height="100%"
        width="100%"
        :data="tableData"
        :border="true"
      >
        <el-table-column
          prop="iccid"
          label="ICCID"
          show-overflow-tooltip
          resizable
          min-width="200"
        >
          <template slot-scope="scope">
            <a class="cor-blue" @click="toDetail(scope.row)">{{ scope.row.iccid }}</a>
          </template>
        </el-table-column>
        <el-table-column
          prop="appProdInstDto.accNum"
          label="MSISDN"
          show-overflow-tooltip
          min-width="140"
        ></el-table-column>
        <el-table-column
          prop="ctgImsi"
          label="IMSI"
          show-overflow-tooltip
          min-width="140"
        ></el-table-column>
        <el-table-column
          prop="hktImsi"
          label="HKT_IMSI"
          show-overflow-tooltip
          min-width="140"
          :formatter="rowFormatter"
        >
        </el-table-column>
        <el-table-column
          prop="appProdInstDto.statusCd"
          :label="$t('simMag.simStatus')"
          :formatter="rowFormatter"
          show-overflow-tooltip
          min-width="130"
        ></el-table-column>
        <el-table-column
          prop="appOfferInstDto.offerName"
          :label="$t('simMag.package')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column
          prop="appCustomerDto.custName"
          :label="$t('simMag.cust')"
          show-overflow-tooltip
          min-width="140"
        ></el-table-column>
        <el-table-column
          prop="appProdInstDto.actDate"
          :label="$t('simMag.actDate')"
          show-overflow-tooltip
          min-width="150"
        ></el-table-column>
        <el-table-column
          prop="totalFlowMB"
          :label="$t('simMag.monthUsage')"
          show-overflow-tooltip
          min-width="150"
        ></el-table-column>
        <el-table-column
          prop="appProdInstDto.addressName"
          :label="$t('simMag.addressId')"
          show-overflow-tooltip
          min-width="200"
          :formatter="rowFormatter"
        ></el-table-column>
      </el-table>
      <el-pagination
        v-show="tableData.length > 0"
        background
        :current-page="page.pageIndex"
        align="right"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="page.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>
    <BatchUpload
      v-if="showUpload"
      :visible.sync="showUpload"
      query-id="2030"
      :is-xls="true"
      :max-file-size="10 * 1024 * 1024"
      :title="$t('simMag.batchImportTitle')"
    >
      <template #tip>
        1、{{ $t('batchUpload.uploadTip1xls') }}<br />
        2、{{ $t('batchUpload.uploadTip2') }}<br />
        3、{{ $t('batchUpload.uploadTip3') }}<br />
        4、{{ $t('batchUpload.uploadTipSimMag', { maxNum }) }}<br />
      </template>
    </BatchUpload>
    <DetailDialog
      :visible.sync="dialogVisible"
      :bulletin-id="bulletinId"
      read-it
    ></DetailDialog>
  </div>
</template>
<script>
import langMixin from '@/mixins/langMixin'
import { mapGetters, mapState } from 'vuex'
import { statusOptions, CT_ADDRESS_ID } from '@/utils/constant'
import fetch from '@/request/fetch'
import DetailDialog from '@/views/sysMag/sysAnn/dialogs/DetailDialog.vue'
import SimStatus from './components/SimStatus'
import BatchUpload from '@/components/BatchUpload'
import BatchExport from '@/components/BatchExport'
const defaultForm = {
  iccid: '',
  areaCode: '',
  msisdn: '',
  imsi: '',
  statusCd: '',
  addressId: '',
  actBeforeDate: '',
  actAfterDate: '',
}
export default {
  components: {
    SimStatus,
    BatchUpload,
    BatchExport,
    DetailDialog,
  },
  mixins: [langMixin],
  data() {
    return {
      showUpload: false,
      powerSearch: false,
      loading: false,
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: [],
      modifyParamDialogVisible: false,
      statusCd: '1000',
      dialogVisible: false,
      bulletinId: '',
      qryParam: { ...defaultForm },
      maxNum: '-',
      modifyForm: {},
      pickerOptions: {
        disabledDate: (time) => {
          if (time.getTime() > Date.now()) return true
          const diff = Math.abs((time - Date.now()) / (1000 * 3600 * 24))
          return diff > 30
        },
      },
    }
  },
  computed: {
    ...mapGetters('user', ['userRoleType']),
    ...mapState('user', ['compPrivs']),
    simListExportParam() {
      return { ...this.qryParam, msisdn: this.areaCodeMsisdn }
    },
    areaCodeMsisdn() {
      const { areaCode, msisdn } = this.qryParam
      if (msisdn) {
        return areaCode + msisdn
      }
      return ''
    },
    statusOptions() {
      return statusOptions.map((item) => {
        return {
          ...item,
          label: this.$t('simMag.' + item.key),
        }
      })
    },
    areaCodeStyle() {
      const width = this.$i18n.locale === 'zh' ? '80px' : '110px'
      return { width }
    },
  },
  mounted() {
    this.querySimCardListForPage(false)
    this.getParamValue()
    this.queryBulletinDialog()
  },
  methods: {
    rowFormatter(row, col, val) {
      if (col.property === 'hktImsi') {
        return row.appProdInstDto.addressId === Number(CT_ADDRESS_ID) ? row.hktImsi : ''
      } else if (col.property === 'appProdInstDto.statusCd') {
        const item = this.statusOptions.find((item) => item.value === val)
        if (item) {
          return this.$t('simMag.' + item.key)
        }
        return val
      } else if (col.property === 'appProdInstDto.addressName') {
        return this.$i18n.locale === 'zh'
          ? row.appProdInstDto.addressName
          : row.appProdInstDto.addressNameEn
      }
    },
    hasBulletinPriv() {
      const hasPrivilege53 = this.compPrivs.some((item) => item.privCode === '53')
      const hasPrivilege54 = this.compPrivs.some((item) => item.privCode === '54')
      return hasPrivilege53 && hasPrivilege54
    },
    queryBulletinDialog() {
      if (!this.hasBulletinPriv) {
        return
      }
      if (localStorage.getItem('bulletinHasPop') === '1') {
        return
      }
      this.loading = true
      const params = {
        type: 'now',
        effective: '1',
        forcePop: 1,
        bulletinType: 0,
        pageIndex: 1,
        pageSize: 1,
      }
      this.$api.sys.bulletin
        .queryBulletinDialog(params)
        .then((res) => {
          if (res) {
            localStorage.setItem('bulletinHasPop', 1)
            this.openBulletDialog(res)
          }
        })
        .finally(() => {
          this.loading = false
        })
    },
    openBulletDialog(row) {
      this.dialogVisible = true
      this.bulletinId = row.bulletinId
    },
    // 查询业务配置
    async querySimCardListForPage(pageChangeFlag) {
      this.loading = true
      if (!pageChangeFlag) {
        this.page.pageIndex = 1
      }
      const params = {
        ...this.qryParam,
        msisdn: this.areaCodeMsisdn,
        pageInfo: {
          ...this.page,
        },
      }
      const res = await fetch('/prodInst/getSimCardPage', params, {
        method: 'post',
      }).finally(() => (this.loading = false))
      res.result?.forEach((row) => (row.totalFlowMB = ''))
      this.tableData = res.result || []
      this.page.total = res.rowCount
      this.qryAllFlowUsage()
    },
    async qryAllFlowUsage() {
      this.tableData.forEach(async (row, index) => {
        const params = {
          msisdn: row.appProdInstDto.accNum,
          prodInstId: row.appProdInstDto.prodInstId,
          custId: row.appProdInstDto.useCustId,
          addressId: row.appProdInstDto.addressId,
        }
        const totalFlowMB = await this.qrySimMonthFlowUsage(params)
        this.$set(this.tableData[index], 'totalFlowMB', totalFlowMB)
      })
    },
    async qrySimMonthFlowUsage(params) {
      const res = await fetch('/prodInst/qrySimMonthFlowUsage', params, {
        method: 'post',
      })
      return res?.totalFlowMB ?? ''
    },
    async getParamValue() {
      this.maxNum =
        (await fetch(
          '/user/getParamValue',
          { paramCode: 'BATCH_QUERY_SIM_MAX_NUM' },
          {
            method: 'get',
          }
        )) ?? '-'
    },
    toDetail(row) {
      this.$router.push({ path: 'simDetail', query: { row: JSON.stringify(row) } })
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.querySimCardListForPage(true)
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.querySimCardListForPage(true)
    },
    // 重置
    reset() {
      this.qryParam = { ...defaultForm }
      this.page = { pageIndex: 1, pageSize: 10, total: 0 }
      this.querySimCardListForPage(false)
    },
  },
}
</script>
<style lang="scss" scoped>
@import '@/assets/css/page.scss';
</style>
